body {
  background-image: -webkit-gradient(linear,left top, left bottom,from(#0f0f0a),to(#3d3d29));
  background-image: -webkit-linear-gradient(#0f0f0a,#3d3d29);
  background-image: -o-linear-gradient(#0f0f0a,#3d3d29);
  background-image: linear-gradient(#0f0f0a,#3d3d29);
}

.box {
  display: inline-block;
  border: 1px solid black;
  width: 15px;
  height: 15px;
  margin-left: -1px;
  margin-bottom: -1px;
  }

.off {
  background-color: lightgray;
}

.on {
  background-color: green;
}

.box:hover {
  background: #00CCFF;
}

.center {
  margin: auto;
  display: table;
  margin-top: 10px;
}

.grid {
  width: 150px;
  line-height: 0;
  margin: auto;
  -webkit-box-shadow: 0px 0px 20px white;
          box-shadow: 0px 0px 20px white;
  margin-top: 20px;
}

h1, h2, h3, h4 {
  color: white;
  margin: auto;
  text-align: center;
  width: 50%;
  margin-top: 10px;
}

h4 {
  margin-top: 0px;
}
.btn{
  border-radius: 0;

}
.himanshu{
  margin:10px auto;
  color:black;
}
.himanshu::after{
  content: "Himanshu sharma";
  color:pink;
  cursor: pointer;
}



